:root {
  --primary: #fe2c55;
  --black: #000;
  --white: #fff;
  --text-color: #161823;
  --layout-header-height: 60px;
  --layout-header-width: 1150px;
  --font-family: 'ProximaNova', sans-serif;

}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


html {
  font-size: 62.5%;
}

body {
  font-family: var(--font-family);
  font-size: 1.7rem;
  line-height: 1.5;
  text-rendering: optimizeSpeed;
  color: #383737;
  overflow-y: overlay;
}

/* // Scrollbar CSS */
html *::-webkit-scrollbar {
  border-radius: 0;
  width: 8px;
}

html *::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.15);
}

html *::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0);
}


button,
input,
[tabindex] {
  outline: none;
  border: none;
}

a {
  color: rgba(22, 24, 35, 1);
  text-decoration: none;
  transition: color 250ms ease 0s;
  cursor: pointer;
}

/* // Fonts emmbeded */
@font-face {
  font-family: ProximaNova;
  src: url('../../assets/fonts/Proxima-Nova-Regular.woff2');
  font-weight: 400;
}

@font-face {
  font-family: ProximaNova;
  src: url('../../assets/fonts/ProximaNova-Semibold.woff2');
  font-weight: 600;
}

@font-face {
  font-family: SofiaPro;
  src: url('../../assets/fonts/sofiapro-semibold.otf');
  font-weight: 600;
}

@font-face {
  font-family: SofiaPro;
  src: url('../../assets/fonts/sofiapro-bold.woff');
  font-weight: 700;
}


/* // Custom tippy tooltip */
body {
  .tippy-box {
    border-radius: 8px;
    font-weight: 600;
    font-size: 1.6rem;
    line-height: 2.2rem;
    background-color: rgba(84, 84, 84, 0.92);
  }

  .tippy-arrow {
    color: rgba(84, 84, 84, 0.92);
  }

  .tippy-content {
    padding: 8px 9px;
  }

  .tippy-box[data-placement^='bottom']>.tippy-arrow:before {
    top: -8px;
  }
}

select option:hover {
  background-color: rgba(22, 24, 35, 0.03);
}