.content_container {
  flex: 0 0 544px;
  width: 544px;
  display: flex;
  flex-direction: column;
  padding: 32px 0px 0px;

  .info {
    padding: 8px 22px 0px;
    margin-bottom: 20px;
  }

  .action {
    position: relative;
    padding: 0px 32px;
    display: flex;
    flex-direction: row;
    -webkit-box-align: center;
    align-items: center;

    .action-btn {
      font-family: var(--font-family);
      font-size: 1.2rem;
      border: none;
      background: none;
      outline: none;
      padding: 0;
      position: relative;
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      cursor: pointer;
      flex-direction: row;
      margin: 0 20px;

    }

    .icon-btn {
      display: flex;
      -webkit-box-pack: center;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      font-size: 0px;
      border-radius: 50%;
      transition: all ease-in-out 200ms;
      background-color: rgba(22, 24, 35, .06);
      margin: 8px 8px 8px 0;


    }

    .liked {
      color: var(--primary);
    }
  }

  .linkCopy {
    color: rgba(22, 24, 35, 0.75);
    line-height: 19px;
    letter-spacing: 0.093px;
    display: flex;
    flex-direction: row;
    margin: 16px 32px;
    box-sizing: border-box;
    border: 1px solid rgba(22, 24, 35, 0.12);
    border-radius: 8px;
    overflow: hidden;

    .link {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      flex: 1 1 auto;
      padding: 7px 0px 5px 12px;
      background-color: rgba(22, 24, 35, 0.06);
    }

    .button {
      border: none;
      background: none rgba(22, 24, 35, 0.06);
      outline: none;
      color: rgb(22, 24, 35);
      font-weight: 700;
      flex: 0 0 auto;
      cursor: pointer;
      padding: 7px 18px;

      &:hover {
        background-color: rgba(22, 24, 35, 0.03);
      }

    }
  }
}


.comment_list_container {
  width: 100%;
  padding: 24px 32px;
  box-sizing: border-box;
  background-color: rgb(248, 248, 248);
  border-top: 1px solid rgba(22, 24, 35, 0.2);
  border-bottom: 1px solid rgba(22, 24, 35, 0.2);
  overflow: hidden auto;
  flex-grow: 1;

  .comment_item_container {

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 16px;
    position: relative;

    .img {
      width: 40px;
      height: 40px;
      border-radius: 50px;
      margin-right: 12px;
    }

    .comment_container {
      flex: 1 1 auto;

      .account_item {
        &:hover {
          text-decoration: underline;
        }
      }

      .comment_user {
        font-weight: 700;
        line-height: 25px;
      }

      .commentText {
        font-size: 16px;
        line-height: 22px;
        white-space: pre-line;
        word-break: break-word;
        margin-bottom: 6px;
        max-width: 358px;
      }
  
      .createdAt {
        color: rgba(22, 24, 35, 0.5);
        font-size: 14px;
        line-height: 20px;
      }
    }

   

    .action_container {
      margin-left: 18px;
      padding-right: 2px;
      width: 24px;
      flex: 0 0 24px;
      display: flex;
      flex-direction: column;
      -webkit-box-align: center;
      align-items: center;

      .like_wrapper {
        color: rgba(22, 24, 35, 0.5);
        font-size: 12px;
        line-height: 17px;
        width: 20px;
        display: flex;
        flex-direction: column;
        -webkit-box-align: center;
        align-items: center;
        cursor: pointer;

        .icon {
          font-size: 20px;
        }

        .liked {
          color: rgb(254, 44, 85);
        }
      }
    }


    .comment_item_container {
      margin-bottom: 16px;
    }
  }
}

.bottom_comment_container {
  flex: 0 0 auto;
  background-color: rgb(255, 255, 255);
  margin: 0px 30px;
  padding: 20px 0px;
  display: flex;

  .input {
    border-radius: 10px;
    background-color: rgb(52 74 192 / 6%);
    padding: 0 10px;
    font-size: 16px;
    width: 100%;
    height: 45px;
  }

  .button {
    margin-left: 25px;
  }
}

.noti_text {
  text-align: center;
}