.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 100px;
    padding: 9px 16px;
    line-height: 10px;
    border-radius: 4px;
    font-size: 1.6rem;
    font-weight: 700;
    font-family: "IBM Plex Sans" , Arial, Tahoma,PingFangSC, sans-serif;
    cursor: pointer;
    background-color: var(--white);
    border: 1px solid transparent;
    user-select: none;

    + .wrapper {
        margin-left: 8px;
    }

    &.disabled {
        pointer-events: all !important;;
        cursor: not-allowed;
        opacity: 0.5;
        background-color: rgba(235, 235, 235, 1) !important;
        color: black !important;

        &:hover {
            background: none;
          }
    }
}

.icon + .title,
.title + .icon {
    margin-left: 6px;
}

.icon {
    display: flex;
    width: 20px;
    text-align: center;
}

/* Button types */
.rounded {
    border-radius: 999px;
    box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
    border-color: rgba(22, 24, 35, 0.12);

    &:hover {
        border-color: rgba(22, 24, 35, 0.2);
        background-color: rgba(22, 24, 35, 0.03);
    }
}

.primary {
    color: var(--white);
    background-color: var(--primary);
    border: none;

    &:hover {
        border-color: var(--primary);
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), #fe2c55;
    }
}

.outline {
    color: var(--primary);
    border-color: currentColor;

    &:hover {
        border-color: currentColor;
        background-color: rgba(254, 44, 85, 0.06);
    }
}

.text {
    font-family: "IBM Plex Sans" ,ProximaNova, Arial, Tahoma,PingFangSC, sans-serif;
    border: 1px solid rgba(22, 24, 35, 0.12);
    
    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

/* // Button sizes */
.small {
    min-width: 88px;
    padding: 4px 16px;
}

.large {
    padding: 14px 16px;
    min-width: 140px;
}


